
import React,{useState} from 'react';
import fire from './config/fire';
import "./Home.css";
import TodoList from './TodoList';


//class Home extends React.Component {
 

 const Home = ()=>
 {


 

const [inputList,SetinputList]=useState("buyapple");
const[Item,SetItem]=useState([]);
 
const itemEvent=(event)=>
{
  SetinputList(event.target.value);

}
 const listofItems=()=>
 {
      SetItem((olditem)=>
      {
        return [...olditem,inputList];
      });
 };


 const deleteItems=(id)=>
 {
console.log("delete");

SetItem((olditem)=>
{
  return olditem.filter((arrElem,index)=>
  {
    return index!==id;
  });
});

 };

  
  const logout=()=>
  {
  fire.auth().signOut();
};
  

  
    return (
      <>
      

      <div style={{textAlign: 'center'}}>
        <h1>You Are Logged In</h1>
        <button onClick = {logout} class="btn btn-success logout">Logout</button>
       
      
      </div>

      <br></br>
      <div className="main_div">
        <div className="center_div">

          <br/>
          <h1>ToDO List</h1>
          <br/>
          <input type="text" className="entry" placeholder="Add items" onChange={itemEvent}/>
          <button  className="btn btn-success" onClick={listofItems}> Add </button>
          <br/>
          <br/>
          <button className=" btn btn-success  clear" onClick={listofItems}> Clear </button>


        <div>
          <ol> 
        
           { Item.map((interval,index)=>
            {
             return  <TodoList  key ={index} 
             id={index}
             text={interval}
             onSelect={deleteItems}
             />
            })};

          </ol>
          </div>
        </div>

      </div>
      </>
      
    )
  }
  


export default Home;