import React from "react";
import "./Home.css";
const TodoList=(probs)=>
{
    return  (
<>
<div className="todo_style">
<i class="fa fa-times" aria-hidden="true" 
onClick={() =>
    {
        probs.onSelect(probs.id)
    }}
    

/>
<li> {probs.text} </li>;
</div>
</>
    );
    
    

};
export default TodoList;